body {
  font-family: Roboto, '微軟正黑體', 'Microsoft JhengHei', sans-serif,
    -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
    Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 1rem;
  background: #f9f9fb;
  color: #777;
  line-height: 1.5;
  padding: 0;
}

h1,
h2,
h4 {
  font-weight: 600;
  font-family: Roboto, '微軟正黑體', 'Microsoft JhengHei', sans-serif,
    -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
    Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol', 'Noto Color Emoji';
  margin-bottom: 0.75rem;
}

.ant-btn.c-btn {
  border-radius: 4px;
  font-weight: 500;
}

.c-btn.is--danger {
  color: #d74c4c;
  border: 1.3px solid #d74c4c;
  background: #fff;
}

.c-btn.is--danger:focus,
.c-btn.is--danger:active,
.c-btn.is--danger:hover {
  color: #fff;
  background: #d74c4c;
  border: 1.3px solid #d74c4c;
}

.c-btn.is--outline {
  color: #2d3159;
  border-color: #2d3159;
  background: transparent;
}

.c-btn.is--outline:hover,
.c-btn.is--outline:focus,
.c-btn.is--outline:active {
  color: #fff;
  background: #2d3159;
  border-color: transparent;
}


.anticon.c-icon:hover {
  color: #38a8a9 !important;
}

